<template>
  <div class="dashboard-time-container" :style="option.style">
    <span v-if="systemShow"><i>{{time[0]}}</i>{{time[1]}}</span>
    <span v-if="waterShow">{{time[1]}}</span>
  </div>
</template>

<script>
  /**
   * 
   * @param timestamp
   * @param type
   * @returns {*}
   */
  function formatTimestamp(timestamp, type) {
    let ts = !timestamp ? new Date().getTime() : typeof(timestamp) === 'string' ? parseInt(timestamp) : timestamp
    type = type === undefined || type === "date" ? "yyyy-MM-dd" : type

    /**
     * 
     * : y; : M; : d; : h; : m; : s; : q; : S
     *  1-4 ;  1  1-3 ;  1-2 
     * @param type {string} 
     */
    let formatFactory = (type) => {

        Date.prototype.format = function (fmt) {
          let o = {
            "M+": this.getMonth() + 1,                      // 
            "d+": this.getDate(),                           // 
            "h+": this.getHours(),                          // 
            "m+": this.getMinutes(),                        // 
            "s+": this.getSeconds(),                        // 
            "q+": Math.floor((this.getMonth() + 3) / 3),    // 
            "S" : this.getMilliseconds()                     // 
          }
          if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length))
          for (let k in o)
            if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)))
          return fmt
        }

        return (new Date(ts)).format(type)
      },

      form = {
        "all"      : "yyyy-MM-dd hh:mm:ss",
        "time"     : "hh:mm:ss",
        "MD"       : "MM-dd",
        "MDHm"     : "MM-dd hh:mm",
        "MDHms"    : "MM-dd hh:mm:ss",
        "DHm"      : "dd hh:mm",
        "YMDHm"    : "yyyy-MM-dd hh:mm",
        "Hm"       : "hh:mm",
        "YM"       : "yyyy-MM",
        "ZH_CN_all": "yyyyMMdd hh:mm:ss",
        "default"  : "yyyy-MM-dd",
        "CH_CN"    : "hh:mm yyyy/MM/dd",
      }

    type = form[type] ? form[type] : type
    return formatFactory(type)
  }

  export default {
    name   : "DashboardTime",
    props  : {
      option     : {
        type: Object,
        default() {
          return {}
        }
      },
      systemShow:{
        type: Boolean,
        default:false
      },
      waterShow:{
        type: Boolean,
        default:false
      },
      styleObject: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    data() {
      return {
        time    : '',
        interval: null
      }
    },
    mounted() {
      this.interval = setInterval(() => {
        this.time = this.getTime()
        this.time = this.time.split(' ')
        //console.log(this.time.split(' '))
        //this.time.split(' ')
      }, 1000)
    },
    methods: {
      getTime() {
        const date = new Date()
        return formatTimestamp(date.getTime(), 'CH_CN')
      }
    },
    beforeDestroy() {
      clearInterval(this.interval)
    }
  }
</script>

<style scoped>
.dashboard-time-container i{
  width:77px;
  height:39px;
  font-size:32px;
  font-family:DIN-Regular,DIN;
  font-weight:400;
  color:rgba(255,255,255,1);
  line-height:39px;
  margin-right: 34px;
}
</style>
