<template>
    <div class="day_amount">
        <div class="border_left_point"></div>
        <div class="border_right_point"></div>
        <div class="block_title">
            <h4>
                <span>10</span>
            </h4>
        </div>
        <div class="block_content">
            <day-amount-chart ref="dayAmountLine" :id="dayAmountLine" :style="{height:'100%',width:'100%'}"/>
        </div>
    </div>
</template>
<script>
    import dayAmountChart from '@/components/charts/dayAmountChart';
    import api from '@/api/waterRelatedData.json'
    import  mixin from '../../mixin'
    export default {
        mixins:[mixin],
        name:'dayAmount',
        components:{
            dayAmountChart
        },
        data(){
            return {
                dayAmountLine:"dayAmountLine",
                lineYData:[],
                lineXData:[],
                devSignData:[],

            }
        },
        methods:{
            //10
            drawDayAmountLineChart(){
                let data = api.data.list;
                data.forEach((item) => {
                    if(this.$store.state.selectId == item.id){
                        this.lineYData = [];
                        this.lineYData = item.dayAmountData;
                        this.$nextTick(()=>{
                            this.$refs.dayAmountLine.initChart(this.lineYData)
                        })
                    }
                })
            },
        },
        mounted() {
            this.drawDayAmountLineChart();
        }
    }

</script>
<style  lang="less" scoped>
    .day_amount{
        width:49.5%;
        height:100%;
        border-top:1px solid rgba(47,136,210,1);
        border-bottom:1px solid rgba(47,136,210,1);
        padding:10px 20px;
        position: relative;
        .border_left_point{
            width:6px;
            height:6px;
            background:rgba(0,197,255,1);
            position:absolute;
            left:0;
            bottom:-3px;
        }
        .border_right_point{
            width:6px;
            height:6px;
            background:rgba(0,197,255,1);
            position:absolute;
            right:0;
            bottom:-3px;
        }
        .block_title{
            width:100%;
            height:12%;
            h4{
                width:100%;
                height:100%;
                text-align: left;
                span{
                    display: inline-block;
                    height:26px;
                    font-size:20px;
                    font-family:MicrosoftYaHei;
                    color:rgba(255,255,255,1);
                    line-height:26px;
                    position: relative;
                }
                span:before{
                    content:'';
                    display: block;
                    width:4px;
                    height:20px;
                    background:rgba(0,197,255,1);
                    position:absolute;
                    left:-12px;
                    top:4px;
                }
            }
        }
        .block_content{
            width: 100%;
            height:88%;
        }
    }
    .day_amount:before{
        content:'';
        display: block;
        width:6px;
        height:6px;
        background:rgba(0,197,255,1);
        position:absolute;
        left:0;
        top:-3px;
    }
    .day_amount:after{
        content:'';
        display: block;
        width:6px;
        height:6px;
        background:rgba(0,197,255,1);
        position:absolute;
        right:0;
        top:-3px;
    }
</style>