import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
      selectId:'32662829238',
  },
  mutations: {
    currentIndex : (state,data) => {
      state.selectId = data;
    }
  },
  actions: {
    selectCurrent({commit},key){
      return new Promise(resolve => {
        commit('currentIndex',key)
      })
    },
  },
  getters:{
    selectId: state => {
      return state.selectId
    }
  },
  modules: {
  }
})
