<template>
    <div class="cust_inform">
        <div class="block_title">
            <h4>
                <span></span>
            </h4>
        </div>
        <div class="block_content">
            <p v-for="item in custInformData">
                <b>{{item.title}}</b><span>{{item.content}}</span>
            </p>
        </div>
    </div>
</template>
<script>
    import api from '@/api/waterRelatedData.json'
    import  mixin from '../../mixin'
    export default {
        mixins:[mixin],
        name:'custInform',
        data(){
            return {
                custInformData:[]
            }
        },
        methods:{
            renderListInfo(){
                let data = api.data.list;
                data.forEach((item) => {
                    if(this.$store.getters.selectId == item.id){
                        this.custInformData = [];
                        this.custInformData = item.custInformData
                    }
                })
            }
        },
        mounted() {
            this.renderListInfo();
        }
    }

</script>
<style  lang="less" scoped>
    .cust_inform{
        width:100%;
        height:40%;
        padding:10px 20px;
        position: relative;
        .block_title{
            width:100%;
            height:10%;
            h4{
                width:100%;
                height:100%;
                text-align: left;
                span{
                    display: inline-block;
                    height:26px;
                    font-size:20px;
                    font-family:MicrosoftYaHei;
                    color:rgba(255,255,255,1);
                    line-height:26px;
                    position: relative;
                }
                span:before{
                    content:'';
                    display: block;
                    width:4px;
                    height:20px;
                    background:rgba(0,197,255,1);
                    position:absolute;
                    left:-12px;
                    top:4px;
                }
            }
        }
        .block_content{
            width: 100%;
            height:90%;
            background: url("../../../../assets/images/consume_inform.svg") no-repeat center right;
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            padding:40px 0 19px 0;
            p{
                width:100%;
                height:22%;
                display: flex;
                justify-content: space-between;
                flex-direction: row;
                align-items: center;
                b{
                    display: flex;
                    justify-content:left;
                    flex-direction: row;
                    align-items: center;
                    width:40%;
                    height:100%;
                    background:linear-gradient(270deg,rgba(2,105,254,0) 0%,rgba(1,103,248,0.2) 100%);
                    font-size:16px;
                    font-family:MicrosoftYaHei;
                    color:rgba(0,197,255,1);
                    line-height:40px;
                    padding-left: 24px;
                    position: relative;
                }
                b:before{
                    content:'';
                    display: block;
                    position:absolute;
                    left:0;
                    top:0;
                    width:2px;
                    height:100%;
                    background:rgba(1,103,248,1);
                    opacity:0.39;
                }
                span{
                    display: flex;
                    justify-content:left;
                    flex-direction: row;
                    align-items: center;
                    width:60%;
                    height:100%;
                    font-size:16px;
                    font-family:MicrosoftYaHei;
                    color:rgba(255,255,255,1);
                    line-height:21px;
                }
            }
        }
    }
    .cust_inform:before{
        content:'';
        display: block;
        width:6px;
        height:6px;
        background:rgba(0,197,255,1);
        position:absolute;
        left:0;
        top:-3px;
    }
    .cust_inform:after{
        content:'';
        display: block;
        width:6px;
        height:6px;
        background:rgba(0,197,255,1);
        position:absolute;
        right:0;
        top:-3px;
    }
</style>