<template>
    <div class="consum_data">
        <div class="month_total">
            <div class="block_div">
                <div class="border_left_top"></div>
                <div class="border_right_top"></div>
                <div class="border_right_bottom"></div>
                <div class="border_left_bottom"></div>
                <div class="month_consume">
                    <countUp
                        v-if="showNum"
                        tag="div"
                        :start-val="consumeTotalData.oldNum"
                        :end-val="consumeTotalData.num"
                        :duration="consumeTotalData.durationValue"
                        :decimals="consumeTotalData.decimalsValue"
                        :roundsOff="false"
                        :options="consumeTotalData.options"
                        class="h4"
                    />
                    <p>m3</p>
                </div>
            </div>
            <div class="block_div">
                <div class="border_left_top"></div>
                <div class="border_right_top"></div>
                <div class="border_right_bottom"></div>
                <div class="border_left_bottom"></div>
                <div class="month_money">
                    <countUp
                        v-if="showNum"
                        tag="div"
                        :start-val="rechargeAmountData.oldNum"
                        :end-val="rechargeAmountData.num"
                        :duration="rechargeAmountData.durationValue"
                        :decimals="rechargeAmountData.decimalsValue"
                        :roundsOff="false"
                        :options="rechargeAmountData.options"
                        class="h4"/>
                    <p></p>
                </div>

            </div>
        </div>
        <div class="table_infom">
            <div class="table_left">
                <div class="block_div" v-for="item in tableInterLeftData">
                    <div class="block_title">
                        <p>{{item.title}}</p>
                    </div>
                    <div class="block_content">
                        <p>{{item.content}}</p>
                    </div>
                </div>
            </div>
            <div class="table_center">
                <div ref="water" style="width:100%;height:100%"></div>
            </div>
            <div class="table_right">
                <div class="block_div" v-for="item in tableInterRightData">
                    <div class="block_title">
                        <p>{{item.title}}</p>
                    </div>
                    <div class="block_content">
                        <p>{{item.content}}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import countUp from '@/components/countUp'
    import api from '@/api/waterRelatedData.json'
    import water from '@/api/shuiwu.json' // json
    import lottie from 'lottie-web'
    import  mixin from '../../mixin'
    export default {
        mixins:[mixin],
        name:'consumData',
        components:{
            countUp
        },
        data(){
            return{
                showNum:true,
                tableInterLeftData:[],
                tableInterRightData:[],
                devTimesData:{},
                intervalconsumeTotalData:4000,
                intervalTotalHouseData:4000,
                intervalrechargeAmountData:4000,
                consumeTotalData: {
                    num: 100,
                    oldNum: 0,
                    decimalsValue: 0,
                    durationValue: 2,
                    percentage: 0,
                    // 
                    options: {
                        format(val) {
                            val = val + "";
                            const valArray = val.split("");
                            return valArray
                                .map(str => {
                                    if (isNaN(+str)) {
                                        return str;
                                    } else {
                                        return `<span class="remake">${str}</span>`;
                                    }
                                })
                                .join("");
                        }
                    }
                },
                rechargeAmountData:{
                    num:100,
                    oldNum: 0,
                    decimalsValue: 0,
                    durationValue: 2,
                    percentage: 0,
                    // 
                    options: {
                        format(val) {
                            val = val + "";
                            const valArray = val.split("");
                            return valArray
                                .map(str => {
                                    if (isNaN(+str)) {
                                        return str;
                                    } else {
                                        return `<span class="remake">${str}</span>`;
                                    }
                                })
                                .join("");
                        }
                    }
                },
                consumeTotalDataTimer:null,
                rechargeAmountTimer:null,
                waterBO:null,
            }
        },
        methods:{
            renderListInfo(){
                let data = api.data.list;
                data.forEach((item) => {
                    if(this.$store.state.selectId == item.id){
                        this.tableInterLeftData = [];
                        this.tableInterRightData = [];
                        this.tableInterLeftData = item.tableInterLeftData;
                        this.tableInterRightData = item.tableInterRightData;
                    }
                })
            },
            renderMonthData(){
                let data = api.data.list;
                data.forEach((item) => {
                    if(this.$store.state.selectId == item.id){
                        this.consumeTotalData.num = item.monthData.consumeTotal;
                        this.rechargeAmountData.num = item.monthData.rechargeAmount;
                    }
                })
            },
            //
            changeconsumeTotalData(intervalTime = 2000) {
                this.consumeTotalDataTimer = setInterval(() => {
                    this.consumeTotalData.oldNum = Number(this.consumeTotalData.num);
                    var plusOrMinus =  Math.pow(-1,Math.round(Math.random() * 10)>=7?1:2);
                    var rangeStep= 30;
                    this.consumeTotalData.num = Number(this.consumeTotalData.num) + Math.round(Math.random() * rangeStep) * plusOrMinus;
                    this.showNum = false; //v-if
                    this.$nextTick(function () {
                        this.showNum = true;
                    })


                }, intervalTime);
            },
            changerechargeAmountData(intervalTime = 2000){
                this.rechargeAmountTimer = setInterval(() => {
                    this.rechargeAmountData.oldNum = Number(this.rechargeAmountData.num);
                    var plusOrMinus =  Math.pow(-1,Math.round(Math.random() * 10)>=7?1:2);
                    var rangeStep= 30;
                    this.rechargeAmountData.num = Number(this.rechargeAmountData.num) + Math.round(Math.random() * rangeStep) * plusOrMinus;
                    this.showNum = false; //v-if
                    this.$nextTick(function () {
                        this.showNum = true;
                    })
                }, intervalTime);
            },
            waterBOAnimation(){
                this.waterBO =  lottie.loadAnimation({
                    animationData:water,  loop:false,
                    autoplay:false,
                    renderer:'svg',
                    container: this.$refs.water, //  //"html""canvas""svg"
                });
                this.waterBO.play()
                setInterval(()=>{
                    this.waterBO.stop()
                    this.waterBO.play()
                },6000)
            },
        },
        mounted() {
            this.renderListInfo();
            this.renderMonthData();
            //
            this.changeconsumeTotalData(this.intervalconsumeTotalData);
            this.changerechargeAmountData(this.intervalrechargeAmountData)
            setTimeout(()=>{
                this.waterBOAnimation();
            },1000)


        },
        destroyed() {
            clearInterval(this.consumeTotalDataTimer)
            clearInterval(this.rechargeAmountTimer)
            this.consumeTotalDataTimer = null;
            this.rechargeAmountTimer = null;
        }
    }

</script>
