export default {
  data(){
    return {}
  },
  computed:{
    selectId(){
      return this.$store.getters.selectId
    }
  },
  methods: {},
  watch:{
    selectId() {
      this.renderListInfo && this.renderListInfo()
      this.drawDayAmountLineChart && this.drawDayAmountLineChart()
      this.drawMonthAmountLineChart && this.drawMonthAmountLineChart()
      this.renderMonthData && this.renderMonthData()
    }
  }
}
