<template>
    <div class="trade_data">
        <div class="block_title">
            <h4>
                <span></span>
            </h4>
        </div>
        <div class="block_content">
            <div class="today_num">
                <div class="total_num">
                    <countUp
                        tag="div"
                        :start-val="tradeAmountData.oldNum"
                        :end-val="tradeAmountData.num"
                        :duration="tradeAmountData.durationValue"
                        :decimals="tradeAmountData.decimalsValue"
                        :roundsOff="false"
                        :options="tradeAmountData.options"
                        class="today_total"/>
                    <p><span></span></p>
                    <h5>()</h5>
                </div>
                <div class="total_num">
                    <countUp
                        tag="div"
                        :start-val="tradeCountData.oldNum"
                        :end-val="tradeCountData.num"
                        :duration="tradeCountData.durationValue"
                        :decimals="tradeCountData.decimalsValue"
                        :roundsOff="false"
                        :options="tradeCountData.options"
                        class="today_total"/>
                    <p><span></span></p>
                    <h5></h5>
                </div>
            </div>
            <div class="trade_sum">
                <p></p>
            </div>
            <div class="trade_dev">
                <div class="dev_computer">
                    <div class="computer">
                        <span><img src="../../../../assets/images/phone.svg"></span>
                        <span>66%</span>
                    </div>
                    <div class="bar">
                        <p></p>
                    </div>
                    <h5><span>20,387</span>&nbsp;&nbsp;</h5>
                </div>
                <div class="dev_phone">
                    <div class="phone">
                        <span>34%</span>
                        <span><img src="../../../../assets/images/computer.svg"></span>
                    </div>
                    <div class="bar">
                        <p></p>
                    </div>
                    <h5>PC&nbsp;&nbsp;<span>11,071</span></h5>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import api from '@/api/systemMointerCenter.json'
    import countUp from '@/components/countUp'
    export default {
        name:'TradeData',
        components:{
            countUp
        },
        data(){
            return {
                devTimesData:api.data.tradeData,
                intervalTimeNBData:12000,
                intervalTimeRealNum:7000,
                tradeAmountData: {
                    num: api.data.tradeData.toadyData.tradeAmount,
                    oldNum: 0,
                    decimalsValue: 0,
                    durationValue: 2,
                    percentage: 0,
                    // 
                    options: {
                        format(val) {
                            val = val + "";
                            const valArray = val.split("");
                            return valArray
                                .map(str => {
                                    if (isNaN(+str)) {
                                        return str;
                                    } else {
                                        return `<span class="remake">${str}</span>`;
                                    }
                                })
                                .join("");
                        }
                    }
                },
                tradeCountData:{
                    num: api.data.tradeData.toadyData.tradeCount,
                    oldNum: 0,
                    decimalsValue: 0,
                    durationValue: 2,
                    percentage: 0,
                    // 
                    options: {
                        format(val) {
                            val = val + "";
                            const valArray = val.split("");
                            return valArray
                                .map(str => {
                                    if (isNaN(+str)) {
                                        return str;
                                    } else {
                                        return `<span class="remake">${str}</span>`;
                                    }
                                })
                                .join("");
                        }
                    }
                }
            }
        },
        methods:{
            //
            changetradeAmountData(intervalTime = 2000) {
                // this.tradeAmountDataTimer = setInterval(() => {
                //     this.tradeAmountData.oldNum = Number(this.tradeAmountData.num);
                //     var plusOrMinus =  Math.pow(-1,Math.round(Math.random() * 10)>=7?1:2);
                //     var rangeStep= 30;
                //     this.tradeAmountData.num = Number(this.tradeAmountData.num) + Math.round(Math.random() * rangeStep) * plusOrMinus;
                // }, intervalTime);
            },
            //
            changetradeCountData(intervalTime = 2000) {
                this.tradeCountDataTimer = setInterval(() => {
                    this.tradeCountData.oldNum = Number(this.tradeCountData.num);
                    var plusOrMinus =  Math.pow(-1,Math.round(Math.random() * 10)>=7?1:2);
                    var rangeStep= 30;
                    this.tradeCountData.num = Number(this.tradeCountData.num) + Math.round(Math.random() * rangeStep) * plusOrMinus;
                }, intervalTime);
            },
        },
        mounted() {
            //
            this.changetradeAmountData(this.intervalTimeNBData);
            this.changetradeCountData(this.intervalTimeRealNum);
        },
        destroyed() {
            clearInterval(this.tradeAmountDataTimer)
            clearInterval(this.tradeCountDataTimer)
            this.tradeCountDataTimer = null;
            this.tradeAmountDataTimer = null;
        }
    }

</script>
