<template>
    <div class="map_container">
        <div id="mapOne" style="height:100%;width:100%;position: absolute;left:0;top:10px;opacity:0.5"></div>
        <div id="mapTwo" style="height:100%;width:100%;position: absolute;left:0;top:2px;opacity:0.5"></div>
        <div id="chinaMap" style="height:100%;width:100%;position: absolute;left:0;top:-6px;"></div>
        <div class="back_china" v-show="showChina" @click="backChina"></div>
    </div>
</template>
<script>
    import echarts from 'echarts';
    import _ from 'lodash'
    import china from '../../../../map/china.json';
    import shanghai from '../../../../map/json/province/shanghai';
    import beijing from '../../../../map/json/province/beijing'
    import hainan from "../../../../map/json/province/hainan"
    import xizang from '../../../../map/json/province/xizang'
    import zhejiang from '../../../../map/json/province/zhejiang'
    import yunnan from '../../../../map/json/province/yunnan'
    import xinjiang from '../../../../map/json/province/xinjiang'
    import tianjin from '../../../../map/json/province/tianjin'
    import sichuan from '../../../../map/json/province/sichuan'
    import shanxi from '../../../../map/json/province/shanxi'
    import shanxi1 from '../../../../map/json/province/shanxi1'
    import shandong from '../../../../map/json/province/shandong'
    import qinghai from '../../../../map/json/province/qinghai'
    import ningxia from '../../../../map/json/province/ningxia'
    import neimenggu from '../../../../map/json/province/neimenggu'
    import jilin from '../../../../map/json/province/jilin'
    import jiangxi from '../../../../map/json/province/jiangxi'
    import jiangsu from '../../../../map/json/province/jiangsu'
    import henan from '../../../../map/json/province/henan'
    import hunan from '../../../../map/json/province/hunan'
    import hubei from '../../../../map/json/province/hubei'
    import heilongjiang from '../../../../map/json/province/heilongjiang'
    import hebei from '../../../../map/json/province/hebei'
    import guangdong from '../../../../map/json/province/guangdong'
    import gansu from '../../../../map/json/province/gansu'
    import chongqing from '../../../../map/json/province/chongqing'
    import aomen from '../../../../map/json/province/aomen'
    import liaoning from '../../../../map/json/province/liaoning'
    import anhui from '../../../../map/json/province/anhui'
    import guangxi from '../../../../map/json/province/guangxi'
    import fujian from '../../../../map/json/province/fujian'
    import xianggang from '../../../../map/json/province/xianggang'
    import guizhou from '../../../../map/json/province/guizhou'
    import taiwan from '../../../../map/json/province/taiwan'
    import api from '@/api/chinaDataList.json'
    import cityApi from '@/api/chinaCityList.json'
   // import pic from '../../../../assets/images/bg_line.png'

    function autoChart(chart, option,that) {
        //
        chart.dispatchAction({
            type: 'downplay',
            seriesIndex: 1,
        });
        const interval = setInterval( ()=> {
            var dataLen = option.series[1].data.length;
            var currentIndex = that.active =='china'? 'chinaCurrentIndex':'cityCurrentIndex'
            // 
            chart.dispatchAction({
                type: 'downplay',
                seriesIndex: 1
            });
            // 
            chart.dispatchAction({
                type: 'highlight',
                seriesIndex: 1,
                dataIndex: that[currentIndex]
            });
            //  tooltip
            chart.dispatchAction({
                type: 'showTip',
                seriesIndex: 1,
                dataIndex: that[currentIndex]
            });
            that[currentIndex] = (that[currentIndex] + 1) % dataLen;
        }, 3000);
        return function () {
            clearInterval(interval)
        }
    }

    export default {
        name:'TableMap',
        data() {
            return {
                chinaCurrentIndex: 0,
                cityCurrentIndex: 0,
               // albumpic_big:pic,
                mapChart: null,
                active: 'china',
                showChina: false,
                cityMap: {
                    'china': {
                        name: '',
                        data: china,
                        code: 'china'
                    },
                    'beijing': {
                        name: '',
                        data: beijing,
                        code: 'beijing'
                    },
                    'hainan': {
                        name: '',
                        data: hainan,
                        code: 'hainan'
                    },
                    'shanghai': {
                        name: '',
                        data: shanghai,
                        code: 'shanghai'
                    },
                    'hebei': {
                        name: '',
                        data: hebei,
                        code: 'hebei'
                    },
                    'shanxi': {
                        name: '',
                        data: shanxi,
                        code: 'shanxi'
                    },
                    "shanxi1": {
                        name: '',
                        data: shanxi1,
                        code: 'shanxi1'
                    },
                    'xinjiang': {
                        name: '',
                        data: xinjiang,
                        code: 'xinjiang'
                    },
                    'xizang': {
                        name: '',
                        data: xizang,
                        code: 'xizang'
                    },
                    'zhejiang': {
                        name: '',
                        data: zhejiang,
                        code: 'zhejiang'
                    },
                    'yunnan': {
                        name: '',
                        data: yunnan,
                        code: 'yunnan'
                    },
                    'tianjin': {
                        name: '',
                        data: tianjin,
                        code: 'tianjin'
                    },
                    'sichuan': {
                        name: '',
                        data: sichuan,
                        code: 'sichuan'
                    },
                    'shandong': {
                        name: '',
                        data: shandong,
                        code: 'shandong'
                    },
                    'qinghai': {
                        name: '',
                        data: qinghai,
                        code: 'qinghai'
                    },
                    'ningxia': {
                        name: '',
                        data: ningxia,
                        code: 'ningxia'
                    },
                    'neimenggu': {
                        name: '',
                        data: neimenggu,
                        code: 'neimenggu'
                    },
                    'jilin': {
                        name: '',
                        data: jilin,
                        code: 'jilin'
                    },
                    'jiangxi': {
                        name: '',
                        data: jiangxi,
                        code: 'jiangxi'
                    },
                    'jiangsu': {
                        name: '',
                        data: jiangsu,
                        code: 'jiangsu'
                    },
                    'henan': {
                        name: '',
                        data: henan,
                        code: 'henan'
                    },
                    'hunan': {
                        name: '',
                        data: hunan,
                        code: 'hunan'
                    },
                    'hubei': {
                        name: '',
                        data: hubei,
                        code: 'hubei'
                    },
                    'heilongjiang': {
                        name: '',
                        data: heilongjiang,
                        code: 'heilongjiang'
                    },
                    'guangdong': {
                        name: '',
                        data: guangdong,
                        code: 'guangdong'
                    },
                    'gansu': {
                        name: '',
                        data: gansu,
                        code: 'gansu'
                    },
                    'chongqing': {
                        name: '',
                        data: chongqing,
                        code: 'chongqing'
                    },
                    'aomen': {
                        name: '',
                        data: aomen,
                        code: 'aomen'
                    },
                    'liaoning': {
                        name: '',
                        data: liaoning,
                        code: 'liaoning'
                    },
                    'anhui': {
                        name: '',
                        data: anhui,
                        code: 'anhui'
                    },
                    'guangxi': {
                        name: '',
                        data: guangxi,
                        code: 'guangxi'
                    },
                    'fujian': {
                        name: '',
                        data: fujian,
                        code: 'fujian'
                    },
                    'xianggang': {
                        name: '',
                        data: xianggang,
                        code: 'xianggang'
                    },
                    'guizhou': {
                        name: '',
                        data: guizhou,
                        code: 'guizhou'
                    },
                    'taiwan': {
                        name: '',
                        data: taiwan,
                        code: 'taiwan'
                    }
                },
                chinaDatas: api.chinaProvinceList,
                defaultP: [
                    // {
                    //     name: '',
                    //     value: 300,
                    //     devNum: '324.424',
                    //     copyTax: '98.57',
                    //     signCover: '98.57',
                    //     aveRsrp: '-49.67',
                    //     aveSinr: '14.23',
                    //     selected: true,
                    // }
                ],
                left: false,
                top: false,
                priority: 'top',
                clearLoop: null
            };
        },
        methods: {
            initChart() {
                let _this = this;
                let mapOne = echarts.init(document.getElementById('mapOne'));
                let mapTwo = echarts.init(document.getElementById('mapTwo'))
                if(!this.mapChart){
                    this.mapChart = echarts.init(document.getElementById('chinaMap'));
                    // 
                    this.mapChart.on('click',  (params)=> {
                       // _this.$refs.mapContain.style.backgroundImage ='none';
                        if (params.seriesType === 'map' && params.data) {
                            const selectData = convertData([{
                                name: params.name,
                                value: params.data.value,
                                devNum: params.data.devNum,
                                copyTax: params.data.copyTax,
                                signCover: params.data.signCover,
                                aveRsrp: params.data.aveRsrp,
                            }])
                            options.geo.regions[0] = {
                                name: params.name,
                                // selected: true
                            }
                            //
                            options.series[0].data = selectData;
                            //
                            let item = _.find(_this.cityMap, (item) => {
                                return item.name === params.name
                            })
                            _this.active = item.code;
                            _this.showChina = true;
                            this.mapChart.dispatchAction({
                                type: 'downplay',
                                seriesIndex: 1
                            });
                            _this.cityCurrentIndex = 0
                            _this.initChart()
                        }
                    });
                    this.mapChart.on("mouseover",  (params)=>{
                        if(!params.data){
                            this.mapChart.dispatchAction({
                                type: 'downplay'
                            });
                        }else{
                            if(this.clearLoop){
                                this.clearLoop()
                                this.clearLoop = null;
                            }
                            this.mapChart.dispatchAction({
                                type: 'downplay',
                                seriesIndex: 1,
                            });
                            this.mapChart.dispatchAction({
                                type: 'highlight',
                                name: params.name,
                            });
                        }
                    });
                    this.mapChart.on("mouseout",  (params)=>{
                        if(params.data){
                            const option = this.mapChart.getOption()
                            if(!this.clearLoop){
                                this.clearLoop = autoChart(this.mapChart, option,this)
                            }
                        }
                    });
                }
                var convertData = function (data) {
                    var res = [];
                    for (var i = 0; i < data.length; i++) {
                        var geoCoord = api.geoCoordMap[data[i].name];
                        if (geoCoord) {
                            res.push({
                                name: data[i].name,
                                value: geoCoord.concat(data[i].value),
                                devNum: geoCoord.concat(data[i].devNum),
                                copyTax: geoCoord.concat(data[i].copyTax),
                                signCover: geoCoord.concat(data[i].signCover),
                                aveRsrp: geoCoord.concat(data[i].aveRsrp),
                                aveSinr: geoCoord.concat(data[i].aveSinr),
                            })
                        }
                    }
                    return res;
                };

                var convertCity = function (data) {
                    var res = [];
                    //su 
                    for(var i in data){
                        res.push({
                            name: data[i].name.indexOf('')>-1?data[i].name:data[i].name+'',
                            value:1212121,
                        })
                    }
                    return res;
                };

                echarts.registerMap(this.cityMap[this.active].code, this.cityMap[this.active].data, {});

                var mapData = _this.active === 'china' ? convertData(this.chinaDatas) : convertCity(cityApi[_this.active])
                let options = {
                    tooltip: {
                        show: true,
                        trigger: 'item',
                        triggerOn:'mousemove',
                        backgroundColor: 'transparent',
                        alwaysShowContent: true,
                        formatter: function (params) {
                            if (_this.active === 'china') {
                                var tipHtml = '';
                                if (params.data) {
                                    tipHtml += '<div class="province_div"></span><div class="border"><span>' + params.data.name + '</span></div></div>';
                                    if(Number(params.data.copyTax[2].split('%').join('')) <= 85){
                                        tipHtml += '<div class="number_div">'
                                            + '<div class="deviceNum"><span></span><em>' + params.data.devNum[2] + '</em></div>'+
                                            '<div class="deviceNum"><span></span><em style="color:red;">' + params.data.copyTax[2] + '</em></div>'+
                                            '<div class="deviceNum"><span></span><em>' + params.data.signCover[2] + '</em></div>'+
                                            '<div class="deviceNum"><span>RSRP</span><em>' + params.data.aveRsrp[2] +'</em></div>'+
                                            '<div class="deviceNum"><span>SINR</span><em>' + params.data.aveSinr[2] + '</em></div>'+
                                            '</div>';
                                    }else if(Number(params.data.aveSinr[2]) <= 10){
                                        tipHtml += '<div class="number_div">'
                                            + '<div class="deviceNum"><span></span><em>' + params.data.devNum[2] + '</em></div>'+
                                            '<div class="deviceNum"><span></span><em>' + params.data.copyTax[2] + '</em></div>'+
                                            '<div class="deviceNum"><span></span><em>' + params.data.signCover[2] + '</em></div>'+
                                            '<div class="deviceNum"><span>RSRP</span><em>' + params.data.aveRsrp[2] +'</em></div>'+
                                            '<div class="deviceNum"><span>SINR</span><em style="color:red;">' + params.data.aveSinr[2] + '</em></div>'+
                                            '</div>';
                                    }else{
                                        tipHtml += '<div class="number_div">'
                                            + '<div class="deviceNum"><span></span><em>' + params.data.devNum[2] + '</em></div>'+
                                            '<div class="deviceNum"><span></span><em>' + params.data.copyTax[2] + '</em></div>'+
                                            '<div class="deviceNum"><span></span><em>' + params.data.signCover[2] + '</em></div>'+
                                            '<div class="deviceNum"><span>RSRP</span><em>' + params.data.aveRsrp[2] +'</em></div>'+
                                            '<div class="deviceNum"><span>SINR</span><em>' + params.data.aveSinr[2] + '</em></div>'+
                                            '</div>';
                                    }

                                    return tipHtml;
                                } else {
                                    return;
                                }
                            } else {
                                var cityName = params.name.slice(0, -1);
                                var cityInfo = cityApi[_this.active][cityName]
                                if (cityInfo) {
                                    var temp = '';
                                    temp += '<div class="province_div"></span><div class="border"><span>' + params.name + '</span></div></div>';
                                    temp += '<div class="number_div">'
                                        + '<div class="deviceNum"><span></span><em>' + cityInfo.devNum + '</em></div>' +
                                        '<div class="deviceNum"><span></span><em>' + cityInfo.copyTax + '</em></div>' +
                                        '<div class="deviceNum"><span></span><em>' + cityInfo.signCover + '</em></div>' +
                                        '<div class="deviceNum"><span>RSRP</span><em>' + cityInfo.aveRsrp + '</em></div>' +
                                        '<div class="deviceNum"><span>SINR</span><em>' + cityInfo.aveSinr + '</em></div>' +
                                        '</div>';
                                    return temp;
                                } else {
                                    return;
                                }
                            }
                        }
                    },
                    geo: {
                        map: this.active, // 
                        roam: false,
                        goDown: true, // 
                        aspectScale: 0.8,// scale  aspect geoBoundingRect.width / geoBoundingRect.height * aspectScale
                        zoom: 1.2,//
                        label: {
                            normal: {
                                show: true,
                                textStyle: {
                                    color: 'rgba(58,175,255,1)',
                                    fontSize: 14,
                                    lineHeight: 19,
                                    align: 'center',
                                },
                            },
                            emphasis: {
                                textStyle: {
                                    color: 'rgba(255,255,255,1)',//
                                    fontSize: 20,
                                    lineHeight: 26,
                                },
                                symbolSize: 14,
                            },
                            rich: {
                                align: 'center',
                                verticalAlign: 'middle',
                            }
                        },
                        itemStyle: {
                            normal: {
                                borderColor: 'rgba(44,116,214,1)', //
                                borderWidth: 1, //
                                areaColor: '#164F99',//
                                label: {
                                    show: true
                                },
                            },
                            emphasis: {
                                areaColor: '#3AAFFF',//
                            },
                        },
                        regions: this.defaultP,
                    },
                    series: [{
                        type: 'effectScatter',
                        coordinateSystem: 'geo',
                        z: 1,
                        data: convertData(this.defaultP),
                        symbol: 'circle',
                        symbolSize: [6, 4],
                        showEffectOn: 'render',
                        rippleEffect: {
                            brushType: 'stroke'
                        },
                        hoverAnimation: true,
                        itemStyle: {
                            color: 'transparent',
                        },
                        tooltip: {
                            show: true
                        }
                    }, {
                        type: 'map',
                        mapType: 'china',
                        geoIndex: 0,
                        z: 0,
                        coordinateSystem: 'geo', // 
                        selectedMode: 'single', //multiple
                        mapLocation: {
                            x: "10%",
                            y: "center"
                        },
                        data: mapData,
                    }]
                };
                this.mapChart.setOption(options)
                if(this.clearLoop){
                    this.clearLoop()
                    this.clearLoop = null
                }
                this.clearLoop = autoChart(this.mapChart, options,this)
                mapOne.setOption({
                    geo: {
                        map: this.active, // 
                        roam: false,
                        goDown: true, // 
                        aspectScale: 0.8,// scale  aspect geoBoundingRect.width / geoBoundingRect.height * aspectScale
                        zoom: 1.2,//
                        itemStyle: {
                            normal: {
                                show: false,
                                borderColor: 'rgba(44,116,214,.5)', //
                                borderWidth: 1, //
                                areaColor: '#164F99',//
                                opacity: .5,
                            }
                        },
                        regions: [{// 
                            name: '',
                            itemStyle: {
                                opacity: 0,
                            }
                        }, { // 
                            name: '',
                            itemStyle: {
                                opacity: 0,
                            }
                        }, { // 
                            name: '',
                            itemStyle: {
                                opacity: 0,
                            }
                        }],
                    },
                })
                mapTwo.setOption({
                    geo: {
                        map: this.active, // 
                        roam: false,//
                        goDown: true, // 
                        aspectScale: 0.8,// scale  aspect geoBoundingRect.width / geoBoundingRect.height * aspectScale
                        zoom: 1.2,//
                        itemStyle: {
                            show: false,
                            normal: {
                                borderColor: 'rgba(44,116,214,.7)', //
                                borderWidth: 1, //
                                areaColor: '#164F99',//
                                opacity: .7,
                            },
                            emphasis: {
                                areaColor: '#3AAFFF',//
                            },
                        },
                        regions: [{// 
                            name: '',
                            itemStyle: {
                                opacity: 0,
                            }
                        }, { // 
                            name: '',
                            itemStyle: {
                                opacity: 0,
                            }
                        }, { // 
                            name: '',
                            itemStyle: {
                                opacity: 0,
                            }
                        }],
                    },
                })
            },
            backChina() {
                this.active = "china";
                //this.$refs.mapContain.style.backgroundImage ='url("'+this.albumpic_big+'")';
                if(this.clearLoop){
                    this.clearLoop();
                    this.clearLoop = null;
                }
                this.initChart();
                this.showChina = false;
            }
        },
        mounted() {
            //this.$refs.mapContain.style.backgroundImage ='url("'+this.albumpic_big+'")';
            this.initChart();
        },
        destroyed() {

        },
    }
</script>
<style lang="less" scoped>
    .map_container {
        width: 100%;
        height: 82%;
        position: relative;
        background: url("../../../../assets/images/bg_map.svg") no-repeat bottom;
        position: relative;
        .back_china {
            position: absolute;
            right: 0;
            bottom: 0;
            width: 80px;
            height: 80px;
            background: url("../../../../assets/images/backChina.svg") no-repeat;
            cursor: pointer;
        }
    }
</style>
<style>
    @font-face {
        font-family: 'Medium';
        src: url("../../../../assets/fonts/DINMedium.ttf");
    }
    /*  */
    /*.province_div {
        width: 200px;
        height: 54px;
        font-size: 24px;
        background: rgba(0, 40, 86, 1);
        opacity: 0.9;
        border: 2px solid rgba(58, 175, 255, 1);
        position: relative;
    }

    .province_div .border span {
        font-family: MicrosoftYaHei;
        color: rgba(58, 175, 255, 1);
        line-height: 54px;
        padding-left: 10px;
    }*/
    .province_div{
        width: 200px;
        height: 54px;
        font-size: 24px;
        background: linear-gradient(136deg, transparent 11px, rgba(0,40,86,1) 0);
        opacity: 0.9;
        border-right: 2px solid rgba(58,175,255,1);
        border-bottom: 2px solid rgba(58,175,255,1);
        position: relative;
    }
    .province_div span{
        position: absolute;
        left:0;
        bottom:0;
        font-family: MicrosoftYaHei;
        color: rgba(58,175,255,1);
        padding-left: 10px;
        width:200px;
        height:38px;
        border-left: 2px solid;
    }
    .province_div:before{
        content: ' ';
        display: block;
        width: 200px;
        height: 2px;
        background: rgba(58,175,255,1);
        position: absolute;
        top: 0px;
        left: 7px;
        transform: scale(0.92);
    }
    .province_div:after{
        content: ' ';
        display: block;
        width: 2px;
        height: 13px;
        background: rgba(58,175,255,1);
        position: absolute;
        top: 1px;
        left: 7px;
        transform: skew(-45deg) scale(1.1);
    }
    .number_div {
        position: absolute;
        left: 6px;
        top: 65px;
        width: 200px;
        height: 184px;
        padding: 10px;
        background: rgba(0, 40, 86, 1);
        opacity: 0.9;
        border: 2px solid rgba(53, 158, 233, 1);
        display: flex;
        justify-content: space-between;
        flex-direction: column;
    }

    .deviceNum {
        width:100%;
        display: flex;
        padding-top:5px;
        justify-content: space-between;
        flex-direction: row;
    }

    .deviceNum span {
        font-size: 16px;
        font-family: MicrosoftYaHei;
        color: rgba(184, 226, 255, 1);
    }

    .deviceNum em {
        font-size: 20px;
        font-family: Medium;
        font-weight: bold;
        color: rgba(255, 255, 255, 1);

    }

</style>

