<template>
    <div class="canvas-map-echarts" id="canvas-map-echarts"></div>
</template>
<script>
    import "echarts-liquidfill";
    import echarts from "echarts";
    export default {
        name: "waterBlueMeterChart",
        data() {
            return {};
        },
        mounted() {
            this.draw();
        },
        methods: {
            draw() {
                let _echarts = echarts.init(
                    document.getElementById("canvas-map-echarts")
                );
                let value = 0.8;
                let data = [];
                data.push(value);
                data.push(value);
                let option = {
                   // backgroundColor: "rgba(59,154,209,.1)",
                    series: [
                        {
                            type: "liquidFill",
                            radius: "100%",
                            data: data,
                            outline: {
                                show: false
                            },
                            borderRadius: "50%",
                            amplitude:6,
                            color: [new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                                {
                                    offset: 0,
                                    color: "rgba(6, 88, 208, .5)"
                                },
                                {
                                    offset: 1,
                                    color: "rgba(48, 153, 214, .5)"
                                }
                            ]), new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                                {
                                    offset: 0,
                                    color: "rgba(6, 88, 208, 1)"
                                },
                                {
                                    offset: 1,
                                    color: "rgba(48, 153, 214, 1)"
                                }
                            ])],
                            backgroundStyle: {
                                borderWidth: 2,
                                // borderColor: 'rgb(255,0,255,0.9)',
                                color: "rgb(255,0,255,0.01)"
                            },
                            itemStyle: {
                                opacity: 1, //
                                shadowBlur: 0, //
                                shadowColor: "#f60" //
                            },
                            label: {
                                normal: {
                                    formatter: (value * 100).toFixed(2) + "%",
                                    textStyle: {
                                        fontSize: 24
                                    }
                                }
                            }
                        }
                    ]
                };
                _echarts.setOption(option);
            }
        }
    };
</script>
<style lang="less">
    .canvas-map-echarts {
        color: #fff;
        height:145px;
        width:145px;
        position: absolute;
        left:0;
        top:0;
    }
</style>