<template>
    <div class="table_status">
        <div class="block_title">
            <h4>
                <span></span>
            </h4>
        </div>
        <div class="block_div">
            <div class="block_warp">
                <div class="block_content" v-for="item in tableStatusData">
                    <p>
                        <span>{{item.tableInform}}</span>
                        <i v-if="item.status == ''" style="color:rgba(26,223,60,1);"></i>
                        <i v-else-if="item.status == ''" style="color:rgba(255,141,72,1);"></i>
                        <i v-else>{{item.status}}</i>
                    </p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import api from '@/api/waterRelatedData.json'
    import  mixin from '../../mixin'
    export default {
        mixins:[mixin],
        name:'tableStatusInform',
        data(){
            return{
                tableStatusData:[],
            }
        },
        methods:{
            renderListInfo(){
                let data = api.data.list;
                data.forEach((item) => {
                    if(this.$store.state.selectId == item.id){
                        this.tableStatusData = [];
                        this.tableStatusData = item.tableStatusData;
                    }
                })
            },
        },
        mounted() {
            this.renderListInfo();
        }
    }

</script>
<style  lang="less" scoped>
    .table_status{
        width:100%;
        height:60%;
        padding:10px;
        border-top:1px solid rgba(47,136,210,1);
        position: relative;
        .block_title{
            width:100%;
            height:6%;
            padding-left: 10px;
            h4{
                width:100%;
                height:100%;
                text-align: left;
                span{
                    display: inline-block;
                    height:26px;
                    font-size:20px;
                    font-family:MicrosoftYaHei;
                    color:rgba(255,255,255,1);
                    line-height:26px;
                    position: relative;
                }
                span:before{
                    content:'';
                    display: block;
                    width:4px;
                    height:20px;
                    background:rgba(0,197,255,1);
                    position:absolute;
                    left:-12px;
                    top:4px;
                }
            }
        }
        .block_div{
            width: 100%;
            height:90%;
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            margin:14px 0;
            .block_warp{
                width:100%;
                height:11%;
                display: flex;
                justify-content: space-between;
                flex-direction: row;
                flex-wrap: wrap;
                .block_content{
                    width:48%;
                    height:100%;
                    display: flex;
                    justify-content: space-between;
                    flex-direction: row;
                    background:rgba(0,51,94,.8);
                    p{
                        width:100%;
                        height:100%;
                        display: flex;
                        justify-content: space-between;
                        flex-direction: row;
                        span{
                            display: flex;
                            justify-content: left;
                            align-items: center;
                            width:60%;
                            height:100%;
                            font-size:12px;
                            font-family:MicrosoftYaHei;
                            color:rgba(255,255,255,.8);
                            line-height:16px;
                            padding-left: 16px;
                        }
                        i{
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            width:40%;
                            height:100%;
                            font-size:12px;
                            font-family:MicrosoftYaHei;
                            color:rgba(26,223,60,1);
                            line-height:16px;
                        }
                    }
                }
                .block_content:nth-child(3){
                     background:transparent;
                }
                .block_content:nth-child(4){
                    background:transparent;
                }
                .block_content:nth-child(7){
                    background:transparent;
                }
                .block_content:nth-child(8){
                    background:transparent;
                }
                .block_content:nth-child(11){
                    background:transparent;
                }
                .block_content:nth-child(12){
                    background:transparent;
                }
                .block_content:nth-child(15){
                    background:transparent;
                }
                .block_content:nth-child(16){
                    background:transparent;
                }
            }


        }
    }
    .table_status:before{
        content:'';
        display: block;
        width:6px;
        height:6px;
        background:rgba(0,197,255,1);
        position:absolute;
        left:0;
        top:-3px;
    }
    .table_status:after{
        content:'';
        display: block;
        width:6px;
        height:6px;
        background:rgba(0,197,255,1);
        position:absolute;
        right:0;
        top:-3px;
    }
</style>