<template>
    <div class="user_top">
        <div class="total_num">
            <div class="total_user"></div>
            <countUp
                    tag="div"
                    :start-val="userTotalData.oldNum"
                    :end-val="userTotalData.num"
                    :duration="userTotalData.durationValue"
                    :decimals="userTotalData.decimalsValue"
                    :roundsOff="false"
                    :options="userTotalData.options"
                    class="num_user"
            />
        </div>
        <div class="house_num">
            <div class="today_enter">
                <p>()&nbsp;&nbsp;</p>
                <countUp
                        tag="div"
                        :start-val="todayHouseData.oldNum"
                        :end-val="todayHouseData.num"
                        :duration="todayHouseData.durationValue"
                        :decimals="todayHouseData.decimalsValue"
                        :roundsOff="false"
                        :options="todayHouseData.options"
                        class="enter"/>
            </div>
            <div class="total_enter">
                <countUp
                    tag="div"
                    :start-val="tableNumData.oldNum"
                    :end-val="tableNumData.num"
                    :duration="tableNumData.durationValue"
                    :decimals="tableNumData.decimalsValue"
                    :roundsOff="false"
                    :options="tableNumData.options"
                    class="enter"/>
                <p>&nbsp;&nbsp;()</p>

            </div>

        </div>
    </div>
</template>
<script>
    import api from '@/api/systemMointerCenter.json'
    import countUp from '@/components/countUp'
    export default {
        name:'TradeData',
        components:{
            countUp
        },
        data(){
            return {
                devTimesData:api.data.totalData,
                intervalUserTotalData:12000,
                intervalTotalHouseData:12000,
                intervalTableNumData:12000,
                userTotalData: {
                    num: api.data.totalData.userTotal,
                    oldNum: 0,
                    decimalsValue: 0,
                    durationValue: 2,
                    percentage: 0,
                    // 
                    options: {
                        format(val) {
                            val = val + "";
                            const valArray = val.split("");
                            return valArray
                                .map(str => {
                                    if (isNaN(+str)) {
                                        return str;
                                    } else {
                                        return `<span class="remake">${str}</span>`;
                                    }
                                })
                                .join("");
                        }
                    }
                },
                todayHouseData:{
                    num: api.data.totalData.todayNum,
                    oldNum: 0,
                    decimalsValue: 0,
                    durationValue: 2,
                    percentage: 0,
                    // 
                    options: {
                        format(val) {
                            val = val + "";
                            const valArray = val.split("");
                            return valArray
                                .map(str => {
                                    if (isNaN(+str)) {
                                        return str;
                                    } else {
                                        return `<span class="remake">${str}</span>`;
                                    }
                                })
                                .join("");
                        }
                    }
                },
                tableNumData:{
                    num: api.data.totalData.tableNum,
                    oldNum: 0,
                    decimalsValue: 0,
                    durationValue: 2,
                    percentage: 0,
                    // 
                    options: {
                        format(val) {
                            val = val + "";
                            const valArray = val.split("");
                            return valArray
                                .map(str => {
                                    if (isNaN(+str)) {
                                        return str;
                                    } else {
                                        return `<span class="remake">${str}</span>`;
                                    }
                                })
                                .join("");
                        }
                    }
                },
                userTotalDataTimer:null,
                todayHouseTimer:null,
                tableNumTimer:null,
            }
        },
        methods:{
            //
            changeUserTotalData(intervalTime = 2000) {
                this.userTotalDataTimer = setInterval(() => {
                    this.userTotalData.oldNum = Number(this.userTotalData.num);
                    var plusOrMinus =  Math.pow(-1,Math.round(Math.random() * 10)>=7?1:2);
                    var rangeStep= 30;
                    this.userTotalData.num = Number(this.userTotalData.num) + Math.round(Math.random() * rangeStep) * plusOrMinus;
                }, intervalTime);
            },
            //
            changeTodayHouseDataData(intervalTime = 2000){
                this.todayHouseTimer = setInterval(() => {
                    this.todayHouseData.oldNum = Number(this.todayHouseData.num);
                    var plusOrMinus =  Math.pow(-1,Math.round(Math.random() * 10)>=7?1:2);
                    var rangeStep= 30;
                    this.todayHouseData.num = Number(this.todayHouseData.num) + Math.round(Math.random() * rangeStep) * plusOrMinus;
                }, intervalTime);
            },
            changeTableNumData(intervalTime = 2000){
                this.tableNumTimer = setInterval(() => {
                    this.tableNumData.oldNum = Number(this.tableNumData.num);
                    var plusOrMinus =  Math.pow(-1,Math.round(Math.random() * 10)>=7?1:2);
                    var rangeStep= 30;
                    this.tableNumData.num = Number(this.tableNumData.num) + Math.round(Math.random() * rangeStep) * plusOrMinus;
                }, intervalTime);
            }
        },
        mounted() {
            //
            this.changeUserTotalData(this.intervalUserTotalData);
            this.changeTodayHouseDataData(this.intervalTotalHouseData);
            this.changeTableNumData(this.intervalTableNumData)
        },
        destroyed() {
            clearInterval(this.userTotalDataTimer)
            clearInterval(this.todayHouseTimer)
            clearInterval(this.tableNumTimer)
            this.userTotalDataTimer = null;
            this.todayHouseTimer = null;
            this.tableNumTimer = null;
        }

    }

</script>
<style lang="less" scoped>
    @font-face {
        font-family: 'Medium';
        src:url("../../../../assets/fonts/DINMedium.ttf");
    }
    .user_top{
        width:100%;
        height:18%;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        .total_num{
            width:100%;
            height:62%;
            background:url("../../../../assets/images/num_bg.svg") no-repeat center center;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size:24px;
            .total_user{
                width:48px;
                height:64px;
                font-family:MicrosoftYaHei;
                color:rgba(0,197,255,1);
                line-height:31px;
                margin-right:2px;
            }
            .num_user{
                /*width:54%;*/
                height:64px;
                font-family:Medium;
                color:rgba(255,255,255,1);
                & /deep/.remake{
                    display: inline-block;
                    width:48px;
                    height:62px;
                    padding:0 8px;
                    margin:0 4px;
                    font-weight:bold;
                    font-size:48px;
                    background:rgba(30,161,234,0.5);
                    box-shadow:0px 0px 12px 0px rgba(30,120,249,.5);
                    border:1px solid rgba(30,161,234,1);
                    color:rgba(255,255,255,1);
                    line-height:64px;
                }

            }
        }
        .house_num{
            width:100%;
            height:38%;
            padding-top:16px ;
            display: flex;
            justify-content: space-between;
            flex-direction: row;
            .today_enter{
                width:50%;
                height:100%;
                display: flex;
                flex-direction: row;
                p{
                    font-size:16px;
                    font-family:MicrosoftYaHei;
                    color:rgba(0,197,255,1);
                    line-height:44px;
                    margin-right:5px;
                }
                & /deep/ .enter{
                    display: inline-block;
                    font-size:32px;
                    font-family:Medium;
                    font-weight:bold;
                    color:rgba(255,255,255,1);
                    line-height:37px;
                }
            }
            .total_enter{
                width:50%;
                height:100%;
                display: flex;
                flex-direction: row-reverse;
                text-align: right;
                p{
                    font-size:16px;
                    font-family:MicrosoftYaHei;
                    color:rgba(0,197,255,1);
                    line-height:44px;
                    margin-right:5px;
                }
                & /deep/ .enter{
                    display: inline-block;
                    font-size:32px;
                    font-family:Medium;
                    font-weight:bold;
                    color:rgba(255,255,255,1);
                    line-height:37px;
                }
            }

        }

    }

</style>