<template>
    <div class="month_check">
        <div class="last_month_bill">
            <div class="bill_date">
                <h4></h4>
                <p>2019-08-07  2019-08-31</p>
            </div>
            <div class="price_num">
                <p><span></span><b>5.50</b></p>
            </div>
        </div>
        <div class="bill_data">
            <div class="block_div">
                <div class="block_content">
                    <div class="border_left"></div>
                    <div class="border_top"></div>
                    <div class="begin_graph">
                        <div ref="start" style="width:100%;height:100%"></div>
                    </div>
                    <countUp
                        tag="div"
                        :start-val="beginNumData.oldNum"
                        :end-val="beginNumData.num"
                        :duration="beginNumData.durationValue"
                        :decimals="beginNumData.decimalsValue"
                        :roundsOff="false"
                        :options="beginNumData.options"
                        class="p"/>
                </div>
                <div class="block_title">
                    <p></p>
                </div>
            </div>
            <div class="block_div">
                <div class="block_content">
                    <div class="border_left"></div>
                    <div class="border_top"></div>
                    <div class="end_graph">
                        <div ref="end" style="width:100%;height:100%"></div>
                    </div>
                    <countUp
                        tag="div"
                        :start-val="endNumData.oldNum"
                        :end-val="endNumData.num"
                        :duration="endNumData.durationValue"
                        :decimals="endNumData.decimalsValue"
                        :roundsOff="false"
                        :options="endNumData.options"
                        class="p"/>
                </div>
                <div class="block_title">
                    <p></p>
                </div>
            </div>
            <div class="block_div">
                <div class="block_content">
                    <div class="border_left"></div>
                    <div class="border_top"></div>
                    <div class="cosume_graph">
                        <div ref="use" style="width:100%;height:100%"></div>
                    </div>
                    <countUp
                        tag="div"
                        :start-val="cousumeNumData.oldNum"
                        :end-val="cousumeNumData.num"
                        :duration="cousumeNumData.durationValue"
                        :decimals="cousumeNumData.decimalsValue"
                        :roundsOff="false"
                        :options="cousumeNumData.options"
                        class="p"/>
                </div>
                <div class="block_title">
                    <p></p>
                </div>
            </div>
            <div class="block_div">
                <div class="block_content">
                    <div class="border_left"></div>
                    <div class="border_top"></div>
                    <div class="money_graph">
                        <div ref="money" style="width:100%;height:100%"></div>
                    </div>
                    <countUp
                        tag="div"
                        :start-val="comsumeMoneyData.oldNum"
                        :end-val="comsumeMoneyData.num"
                        :duration="comsumeMoneyData.durationValue"
                        :decimals="comsumeMoneyData.decimalsValue"
                        :roundsOff="false"
                        :options="comsumeMoneyData.options"
                        class="p"/>
                </div>
                <div class="block_title">
                    <p></p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import api from '@/api/waterRelatedData.json'
    import countUp from '@/components/countUp'
    import lottie from 'lottie-web'
    import money from '@/api/moneyData.json' // json
    import end from '@/api/endData.json'
    import start from '@/api/startData.json'
    import use from '@/api/useData.json'
    export default {
        name:'monthCheck',
        components:{
            countUp
        },
        data(){
            return {
                devTimesData:api.data.monthData,
                intervalbeginNumData:5000,
                intervalendNumData:5000,
                intervalconsumeNumData:5000,
                intervalconsumeMoneyData:5000,
                beginNumData:{
                    num: api.data.monthData.beginNum,
                    oldNum: 0,
                    decimalsValue: 0,
                    durationValue: 2,
                    percentage: 0,
                    // 
                    options: {
                        format(val) {
                            val = val + "";
                            const valArray = val.split("");
                            return valArray
                                .map(str => {
                                    if (isNaN(+str)) {
                                        return str;
                                    } else {
                                        return `<span class="remake">${str}</span>`;
                                    }
                                })
                                .join("");
                        }
                    }
                },
                endNumData:{
                    num: api.data.monthData.endNum,
                    oldNum: 0,
                    decimalsValue: 0,
                    durationValue: 2,
                    percentage: 0,
                    // 
                    options: {
                        format(val) {
                            val = val + "";
                            const valArray = val.split("");
                            return valArray
                                .map(str => {
                                    if (isNaN(+str)) {
                                        return str;
                                    } else {
                                        return `<span class="remake">${str}</span>`;
                                    }
                                })
                                .join("");
                        }
                    }
                },
                cousumeNumData:{
                    num: api.data.monthData.consumeNum,
                    oldNum: 0,
                    decimalsValue: 0,
                    durationValue: 2,
                    percentage: 0,
                    // 
                    options: {
                        format(val) {
                            val = val + "";
                            const valArray = val.split("");
                            return valArray
                                .map(str => {
                                    if (isNaN(+str)) {
                                        return str;
                                    } else {
                                        return `<span class="remake">${str}</span>`;
                                    }
                                })
                                .join("");
                        }
                    }
                },
                comsumeMoneyData:{
                    num: api.data.monthData.consumeMoney,
                    oldNum: 0,
                    decimalsValue: 0,
                    durationValue: 2,
                    percentage: 0,
                    // 
                    options: {
                        format(val) {
                            val = val + "";
                            const valArray = val.split("");
                            return valArray
                                .map(str => {
                                    if (isNaN(+str)) {
                                        return str;
                                    } else {
                                        return `<span class="remake">${str}</span>`;
                                    }
                                })
                                .join("");
                        }
                    }
                },
                beginNumTimer:null,
                endNumTimer:null,
                consumeNumTimer:null,
                consumeMoneyTimer:null,
                moneyBO:null,
                startBo:null,
                endBo:null,
                useBo:null,
            }
        },
        methods:{
            changebeginNumData(intervalTime = 2000){
                this.beginNumTimer = setInterval(() => {
                    this.beginNumData.oldNum = Number(this.beginNumData.num);
                    var plusOrMinus =  Math.pow(-1,Math.round(Math.random() * 10)>=7?1:2);
                    var rangeStep= 30;
                    this.beginNumData.num = Number(this.beginNumData.num) + Math.round(Math.random() * rangeStep) * plusOrMinus;
                }, intervalTime);
            },
            changeendNumData(intervalTime = 2000){
                this.endNumTimer = setInterval(() => {
                    this.endNumData.oldNum = Number(this.endNumData.num);
                    var plusOrMinus =  Math.pow(-1,Math.round(Math.random() * 10)>=7?1:2);
                    var rangeStep= 30;
                    this.endNumData.num = Number(this.endNumData.num) + Math.round(Math.random() * rangeStep) * plusOrMinus;
                }, intervalTime);
            },
            changecomsumeNumData(intervalTime = 2000){
                this.endNumTimer = setInterval(() => {
                    this.cousumeNumData.oldNum = Number(this.cousumeNumData.num);
                    var plusOrMinus =  Math.pow(-1,Math.round(Math.random() * 10)>=7?1:2);
                    var rangeStep= 30;
                    this.cousumeNumData.num = Number(this.cousumeNumData.num) + Math.round(Math.random() * rangeStep) * plusOrMinus;
                }, intervalTime);
            },
            changecomsumeMoneyData(intervalTime = 2000){
                this.consumeMoneyTimer = setInterval(() => {
                    this.comsumeMoneyData.oldNum = Number(this.comsumeMoneyData.num);
                    var plusOrMinus =  Math.pow(-1,Math.round(Math.random() * 10)>=7?1:2);
                    var rangeStep= 30;
                    this.comsumeMoneyData.num = Number(this.comsumeMoneyData.num) + Math.round(Math.random() * rangeStep) * plusOrMinus;
                }, intervalTime);
            },
            startBoAnimation(){
                this.startBo =  lottie.loadAnimation({
                    animationData:start,  loop:false,
                    autoplay:false,
                    renderer:'svg',
                    container: this.$refs.start, //  //"html""canvas""svg"
                });
                this.startBo.play()
                setInterval(()=>{
                    this.startBo.stop()
                    this.startBo.play()
                },6000)
            },
            endBoAnimation(){
                this.endBo =  lottie.loadAnimation({
                    animationData:end,  loop:false,
                    autoplay:false,
                    renderer:'svg',
                    container: this.$refs.end, //  //"html""canvas""svg"
                });
                this.endBo.play()
                setInterval(()=>{
                    this.endBo.stop()
                    this.endBo.play()
                },6000)
            },
            moneyBoAnimation(){
                this.moneyBO =  lottie.loadAnimation({
                    animationData:money,  loop:false,
                    autoplay:false,
                    renderer:'svg',
                    container: this.$refs.money, //  //"html""canvas""svg"
                });
                this.moneyBO.play()
                setInterval(()=>{
                    this.moneyBO.stop()
                    this.moneyBO.play()
                },6000)
            },
            useBoAnimation(){
                this.useBO =  lottie.loadAnimation({
                    animationData:use,  loop:true,
                    autoplay:true,
                    renderer:'svg',
                    container: this.$refs.use, //  //"html""canvas""svg"
                });
                this.useBO.play()
            }

        },
        mounted() {
            //
            this.changebeginNumData(this.intervalbeginNumData)
            this.changeendNumData(this.intervalendNumData)
            this.changecomsumeNumData(this.intervalconsumeNumData)
            this.changecomsumeMoneyData(this.intervalconsumeMoneyData)

            setTimeout(()=>{
                this.startBoAnimation();
                this.moneyBoAnimation();
                this.endBoAnimation();
                this.useBoAnimation();
            },1000)


        },
        destroyed() {
            clearInterval(this.beginNumTimer)
            clearInterval(this.endNumTimer)
            clearInterval(this.consumeNumTimer)
            clearInterval(this.consumeMoneyTimer)
            this.beginNumTimer = null;
            this.endNumTimer = null;
            this.consumeNumTimer = null;
            this.consumeMoneyTimer = null;
        }
    }

</script>
<style  lang="less" scoped>
    @font-face {
        font-family: 'Medium';
        src:url("../../../../assets/fonts/DINMedium.ttf");
    }
    .month_check{
        width:35%;
        height:100%;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        padding-bottom:20px;
        .last_month_bill{
            width:100%;
            height:14%;
            background: url("../../../../assets/images/last_month.svg") no-repeat center center;
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-direction: row;
            .bill_date{
                width:50%;
                height:100%;
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                padding:12px 0 12px 26px;
                text-align:left;
                h4{
                    width:100%;
                    height:33px;
                    font-size:24px;
                    font-family:Medium;
                    font-weight:500;
                    color:rgba(255,255,255,1);
                    line-height:33px;
                    margin-bottom: 4px;
                }
                p{
                    width:100%;
                    height:19px;
                    font-size:14px;
                    font-family:MicrosoftYaHei;
                    color:rgba(147,168,207,1);
                    line-height:19px;
                }
            }
            .price_num{
                width:50%;
                height:100%;
                p{
                    width:100%;
                    height:100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex-direction: row;
                    span{
                        display: inline-block;
                        font-size:16px;
                        font-family:MicrosoftYaHei;
                        color:rgba(244,88,87,1);
                        line-height:21px;
                        margin-right:16px;
                        position: relative;
                    }
                    span:before{
                        content:'';
                        display: block;
                        width:6px;
                        height:6px;
                        background:rgba(244,88,87,1);
                        position:absolute;
                        left:-14px;
                        top:9px;
                    }
                    b{
                        display: inline-block;
                        font-size:26px;
                        font-family:DINAlternate-Bold,DINAlternate;
                        font-weight:bold;
                        color:rgba(255,255,255,1);
                        line-height:30px;
                    }

                }
            }
        }
        .bill_data{
            width:100%;
            height:84%;
            display: flex;
            justify-content: space-between;
            flex-direction: row;
            flex-wrap: wrap;
            padding:0 12px;
            .block_div{
                width:48%;
                height:48%;
               .block_content{
                   width:100%;
                   height:80%;
                   background:linear-gradient(135deg, transparent 15px, rgba(0,38,77,0.8) 0);
                   border-right:1px solid rgba(47,136,210,1);
                   position: relative;
                   padding:14px 32px 0 32px;
                   .begin_graph{
                       width:100%;
                       height:70%;
                       //background: url("../../../../assets/images/beginNum.svg") no-repeat center center;
                   }
                   .end_graph{
                       width:100%;
                       height:70%;
                      // background: url("../../../../assets/images/endNum.svg") no-repeat center center;
                   }
                   .cosume_graph{
                       width:100%;
                       height:70%;
                      // background: url("../../../../assets/images/consume.svg") no-repeat center center;
                   }
                   .money_graph{
                       width:100%;
                       height:70%;
                       //background: url("../../../../assets/images/money.svg") no-repeat center center;
                   }
                   .p{
                       width:100%;
                       height:30%;
                       font-size:30px;
                       font-family:Medium;
                       font-weight:bold;
                       color:rgba(255,255,255,1);
                       line-height:37px;
                   }
               }
                .block_content:before{
                    content:'';
                    display: block;
                    position:absolute;
                    left:10px;
                    top:0;
                    width:1px;
                    height:20px;
                    border-left: 1px solid rgba(47,136,210,1);
                    transform: skew(-45deg);
                }
                .border_left{
                    position:absolute;
                    left:0;
                    top:20px;
                    width:1px;
                    height:calc(100% - 20px);
                    border-left: 1px solid rgba(47,136,210,1);
                }
                .border_top{
                    position:absolute;
                    left:20px;
                    top:0;
                    width:calc(100% - 20px);
                    height:1px;
                    border-top: 1px solid rgba(47,136,210,1);
                }
                .block_title{
                    width:100%;
                    height:20%;
                    background: linear-gradient(-45deg, transparent 15px, rgba(13,83,143,1) 0);
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    p{
                        font-size:16px;
                        font-family:MicrosoftYaHei;
                        color:rgba(255,255,255,1);
                        line-height:21px;
                        background:linear-gradient(180deg, rgba(238,238,238,1) 0%, rgba(216,216,216,1) 100%);
                        -webkit-background-clip:text;
                        -webkit-text-fill-color:transparent;
                    }
                }
            }
        }
    }
</style>